.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
  }
  
  .product_img {
    display: flex;
  }
  
  .pro1 {
    max-height: 100px;
    width: 50%;
    object-fit: contain;
    margin-bottom: 5px;
  }
  
  
  .pro2 {   max-height: 100px;
    width: 50%;
    object-fit: contain;
    margin-bottom: 5px;
  }
  
  
  
  
  .pro4 {
    max-height: 100px;
    width: 50%;
    object-fit: contain;
    margin-bottom: 5px;
  }
  .p1{
    background: #f0c14b;
    border: 1px solid;
    margin-bottom: 1px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  .p2{
    background: #f0c14b;
    border: 1px solid;
    margin-bottom: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  .p3{
    background: #f0c14b;
    border: 1px solid;
    margin-bottom: 25px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  .p4{
    background: #f0c14b;
    border: 1px solid;
    margin-bottom: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  
  .product__info {
    height: 100px;
    margin-bottom: 15px;
  }